@font-face {
  font-family: "DM Sans";
  src: local("DM Sans Regular"), local("DM Sans-Regular"),
    url("../fonts/DMSans-Regular.woff2") format("woff2"),
    url("../fonts/DMSans-Regular.woff") format("woff"),
    url("../fonts/DMSans-Regular.ttf") format("truetype"),
    url("../fonts/DMSans-Regular.eot") format("eot");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "DM Sans";
  src: local("DM Sans Medium"), local("DM Sans-Medium"),
    url("../fonts/DMSans-Medium.woff2") format("woff2"),
    url("../fonts/DMSans-Medium.woff") format("woff"),
    url("../fonts/DMSans-Medium.ttf") format("truetype"),
    url("../fonts/DMSans-Medium.eot") format("eot");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "DM Sans";
  src: local("DM Sans Bold"), local("DM Sans-Bold"),
    url("../fonts/DMSans-Bold.woff2") format("woff2"),
    url("../fonts/DMSans-Bold.woff") format("woff"),
    url("../fonts/DMSans-Bold.ttf") format("truetype"),
    url("../fonts/DMSans-Bold.eot") format("eot");
  font-weight: 700;
  font-style: normal;
}
